<template>
  <div>
    <b-container fluid :style="cssVars">
      <b-row>
        <b-col class="p-5 header-inicio" style="">
          <h2>Contacto</h2>
          <p><b-icon icon="geo-alt"> </b-icon> {{ configuracion.nombre }}</p>
        </b-col>
      </b-row>
    </b-container>

    <b-container :style="cssVars">
      <b-row class="py-5">
        <b-col clos="5" class="p-4" style="text-aling: left">
          <h2 class="pb-4 mh-heading--bottom-separator">Contactanos</h2>
          <b-form>
            <b-form-group id="input-group-1">
              <b-form-group id="input-group-2">
                <b-form-input
                  id="input-2"
                  v-model="form.nombre"
                  placeholder="Ingrese su nombre"
                  required
                ></b-form-input>
              </b-form-group>

              <b-form-input
                id="input-1"
                v-model="form.email"
                type="email"
                placeholder="Ingrese su email"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-group id="input-group-2">
              <b-form-input
                id="input-2"
                v-model="form.telefono"
                placeholder="Ingrese Su Teléfono"
                required
              ></b-form-input>
            </b-form-group>

            <b-form-textarea
              id="textarea"
              v-model="form.mensaje"
              placeholder="Ingrese su mensaje..."
              rows="3"
              max-rows="6"
            ></b-form-textarea>

            <b-row>
              <b-col class="botones mt-2">
                <b-button class="" @click="sendContacto()" variant="naranja"
                  >Enviar</b-button
                >
              </b-col>
            </b-row>

            <b-row v-if="form.mensaje_contacto">
              <b-col>
                <b-alert variant="success" show>{{
                  form.mensaje_contacto
                }}</b-alert>
              </b-col>
            </b-row>
          </b-form>
        </b-col>

        <b-col cols="8" class="p-1">
          <b-row class="h-100" style="height: 100%">
            <b-col class="contacto6 p-4">
              <h2 class="pb-4 mh-heading--bottom-separator">Donde Ubicarnos</h2>

              <p v-if="configuracion">
                <b-icon icon="map"> </b-icon>
                {{ configuracion.id_ciudad.name }}
                {{ configuracion.id_estado.name }}
              </p>
              <p v-if="configuracion">
                <b-icon icon="telephone"> </b-icon> {{ configuracion.telefono }}
              </p>
              <p v-if="configuracion">
                <b-icon icon="envelope"> </b-icon> {{ configuracion.email }}
              </p>
            </b-col>
          </b-row>
        </b-col>
      </b-row>

      <!-- buscador -->
    </b-container>

    <b-container fluid>
      <b-row>
        <b-col class="p-0">
          <iframe
            src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d10633.00223485022!2d-76.54191093635883!3d3.3805694377123796!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8e30a6f0cc4bb3f1%3A0x1f0fb5e952ae6168!2sCali%2C%20Valle%20del%20Cauca%2C%20Colombia!5e0!3m2!1ses!2sve!4v1641648688094!5m2!1ses!2sve"
            width="100%"
            height="450"
            style="border: 0"
            allowfullscreen=""
            loading="lazy"
          ></iframe>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
export default {

  data() {
      return {
        form: {
          email: '',
          nombre: '',
          telefono: '',
          mensaje: '',
        },
       
    
      }
    },
 
  name: 'Miguel Machado',
  slide: 0,
  sliding: null,
  states: [
    { state: 'Bogota' },
    { state: 'Cucuta' },
    { state: 'Medellin' },
    { state: 'Cali' }
  ],
  props: {
    msg: String
  },
  form: {
    email: '',
    name: '',
    food: null,
    checked: []
  },

  computed:{

    inmuebles(){
      return this.$store.state.inmuebles
    },
    usuario(){
      return this.$store.state.usuario
    },

    blogs(){

     //  lista.sort(function() {return Math.random() - 0.5})
      return this.$store.state.blogs.data.data.blogs.slice(0,3)
    },
    code(){
      return this.$store.state.code
    },

    banners(){
      return this.$store.state.banners
    },

     configuracion(){
      return this.$store.state.configuracion
    },
    

    cssVars() {
      return {
        '--bg-color-primario': this.$store.state.color.primario,
        '--bg-color-secundario': this.$store.state.color.secundario,
      }
    }



  },

  created() {
     // this.getInmuebles();

       this.$store.dispatch('getCode',window.location.href.split('//')[1].split('.')[0].split(':')[0])
      .then(()=>this.loading=false);

      this.$store.dispatch('getInmuebles', this.code)
      .then(()=>(
        this.setTitulo()
      ))

      

  },

  
  methods: {
    onSlideStart(slide) {
      this.sliding = true;
      this.slide=slide;
    },
    onSlideEnd(slide) {
      this.sliding = false;
      this.slide=slide;

    },
    sendContacto() {
      
      let formData = new FormData();

      formData.append("user_id", this.usuario.id);
      formData.append("full_name", this.form.nombre);
      formData.append("email", this.form.email);
      formData.append("celular", this.form.telefono);
      formData.append("message", this.form.mensaje);

      console.log(formData);

        this.$store
          .dispatch("addContacto", formData)
          .then((response) => {
            this.form.mensaje_contacto='Mensaje Enviado satisfactoriamente';
            this.form.nombre='';
            this.form.email='';
            this.form.mensaje='';
            this.form.telefono='';
           // this.$router.push({ name: "banners-list" });
           console.log(response)
          })
          .catch((error) => {
           console.log(error)
          });
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      },
      
     setTitulo(){
      document.title = this.$store.state.configuracion.nombre;
     // document.getElementById('pageFavicon').attr('href', 'https://res.cloudinary.com/dm5nedbyd/image/upload/c_fill,h_32,w_32/v1644445047/ixgp6fzzqbvhbmbxclhd.webp');
      const favicon = document.getElementById("pageFavicon");

      if(this.$store.state.configuracion.logo.substr(0, 4) == 'http'){

        favicon.href=this.$store.state.configuracion.logo.split('upload')[0]+'upload/c_fill,h_32,w_32'+this.$store.state.configuracion.logo.split('upload')[1];

      }else{

        favicon.href=this.url+this.$store.state.configuracion.logo;
      }

    }


  }
}
</script>

